import React from "react";
import Typography from "@material-ui/core/Typography";
import { jsxDecorator } from "storybook-addon-jsx";
import { Paper, Divider, Grid } from "@material-ui/core";
import { makeStyles, MuiThemeProvider } from "@material-ui/core/styles";
import { storiesOf } from "@storybook/react";
import { muiTheme } from "storybook-addon-material-ui";
import themes from "../../Theme/themes";
import Theme from "../../Theme";
import Avaliation from "../Avaliation";

const useStyles = makeStyles((theme) => ({
    root: {
        boxShadow: theme.shadows[24],
        margin: "20px",
        padding: "20px",
    },
    darkBg: {
        background: "rgba(0, 0, 0, 0.6)",
        margin: "20px",
        padding: "20px",
    },
}));

export const DefaultAvaliation = ({ label, mode, brand, size, color }) => {
    const classes = useStyles();

    return (
        <MuiThemeProvider theme={themes[brand]}>
            <Paper className={classes[mode]} id="avaliation">
                <Typography variant="h3">Avaliation</Typography>
                <br />
                <Divider />
                <br />

                <Grid container spacing={2}>
                    <Grid item xs={12} lg={6}>
                        <Avaliation label={label} size={size} color={color} />
                    </Grid>
                </Grid>
            </Paper>
        </MuiThemeProvider>
    );
};

storiesOf("Components/Avaliation", module)
    .addDecorator(muiTheme([Theme]))
    .addDecorator(jsxDecorator)
    .add("Default ", (args) => <DefaultAvaliation {...args} />,
        {
            args: {
                label: "25",
                brand: "CVC",
                mode: "root",
                size: 'medium',
                color: 'primary',
            },
            argTypes: {
                label: { type: "string" },
                brand: {
                    type: "select",
                    options: ["BlackAndWhite", "BlackFriday", "CVC", "CVCCorp", "CVCYellow", "Sub"]
                },
                mode: {
                    type: "select",
                    options: ["root", "darkBg"]
                },
                size: {
                    options: ['small', 'medium', 'large'],
                    control: { type: 'select' }
                },
                color: {
                    options: ['default', 'primary', 'secondary'],
                    control: { type: 'select' }
                }
            }
        });
